import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
// import { BeatLoader } from "react-spinners";
import "./MaterialMap.css";
import { getExcelColumnNumber, getProperColumnAddress } from "../../../constFunctions/excelFunction";
import DropDown from "../../DropDown/DropDown";
import Button from "../../Button/Button";
import {
  checkAlreadyReserved,
  logout,
  showErrorMessage,
  successMessage,
} from "../../../constFunctions/copmponentFunctions";
import CloseLogo from "../../../../../assets/icons/x-circle.svg";
import EditLogo from "../../../../../assets/icons/editLogo.svg";
import { runMutation, runQuery } from "../../../api/apiCalls";
import { BOM__MAPPING_FIELDS, JWT_ERROR_MESSAGE, MATAERIAL_MAPPING_FIELDS } from "../../../constFunctions/constants";
import { mappingConfigDataMutation } from "../../../api/graphQlMutation";
import { DTX_GRAPHQL_URL } from "../../../../config";
import { setBomMappingTempData2, setMMmappingTempData2, setUploadLoader } from "../../../redux-toolkit/slice/action";
import { setBomMappingData, setMMmappingData, setNextPage } from "../../../redux-toolkit/slice/TaskpaneSlice";
import { GetBomMaterialMappingConfigQuery } from "../../../api/graphQlQuery";

function MaterialMap({ mappingConfig, setmappingConfig, setmappingConfig2 }) {
  const { nextPage } = useSelector((state) => state.taskpane);
  const { bomMappingDataTemp, MMmappingDataTemp } = useSelector((state) => state.action);
  const [rangeFor, setRangeFor] = useState({});
  const [edit, setEdit] = useState(false);
  const dispatch = useDispatch();
  const fieldFor = nextPage === "bom" ? BOM__MAPPING_FIELDS : MATAERIAL_MAPPING_FIELDS;
  useEffect(() => {
    getLatestMappingData();
  }, [true]);

  const selectRangeFor = (rangeFor) => {
    setRangeFor(rangeFor);
  };
  const getLatestMappingData = () => {
    dispatch(setUploadLoader(true));
    try {
      const body = {
        operationName: "GetBomMaterialMappingConfigQuery",
        query: GetBomMaterialMappingConfigQuery,
        variables: { tenant_id: localStorage.getItem("tenantId") },
      };
      runQuery(DTX_GRAPHQL_URL, localStorage.getItem("tenantToken"), body)
        .then((data) => {
          if (data.errors) {
            data.errors.map((element) => {
              if (element?.message.includes(JWT_ERROR_MESSAGE)) {
                logout();
                dispatch(setNextPage("homeLogin"));
              }
            });
          } else {
            dispatch(setMMmappingData(data?.data?.tenant_defaults[0].material));
            dispatch(setBomMappingData(data?.data?.tenant_defaults[0].bom));
            dispatch(setMMmappingTempData2(data?.data?.tenant_defaults[0].material));
            dispatch(setBomMappingTempData2(data?.data?.tenant_defaults[0].bom));
            dispatch(setUploadLoader(false));
          }
        })
        .catch((error) => {
          console.log(error.message, "error.message");
          dispatch(setUploadLoader(false));
        });
    } catch (error) {
      console.log(error);
    } finally {
    }
  };

  //function  reserveSelectedSpace  which stores selected address into MMmapping data (state in redux)
  // you have to select what kind of region you are looking for ,if we want select entire colummn then need to select "column" from range type
  // if first column of the excel has selected the the address by default  "A:A" or "A1:Ax" or (if you selected a region like "A10:C20") will be changed to "A"
  //mappingConfig is an object will store the selected address
  // mappingConfig={
  // custom_material_id:{ name: "Custom Material Id* ", address: "A" }
  //}
  const reserveSelectedSpace = async () => {
    try {
      rangeFor?.label &&
        (await Excel.run(async (context) => {
          const range = context.workbook.getSelectedRange();
          range.load("address");
          await context.sync();
          let selectedAddress = getProperColumnAddress(range.address);
          //checkAlreadyReserved is for checking whether the selelcted colum is reserved or
          checkAlreadyReserved(mappingConfig, selectedAddress).then((columncheck) => {
            if (nextPage === "bom" ? selectedAddress !== "A" && !columncheck : true) {
              dispatch(
                setmappingConfig({
                  name: rangeFor.label,
                  value: rangeFor.value,
                  address: selectedAddress,
                  columnNumber: getExcelColumnNumber(selectedAddress),
                })
              );
              let selectedRange = context.workbook.worksheets
                .getActiveWorksheet()
                .getRange(String(selectedAddress + 1));
              selectedRange.format.fill.color = "#FFF0D9";
              nextPage === "bom" && (selectedRange.values = [[rangeFor.label]]);
              selectedRange.format.autofitRows();
              selectedRange.format.autofitColumns();
              return context.sync();
            } else {
              showErrorMessage(
                "reserved-column-error",
                ` Column ${selectedAddress}  is a reserved space`,
                "apply-button"
              );
            }
          });
        }));
    } catch (error) {
      console.log("Error:", error);
    }
  };

  const saveMMmappingConfig = () => {
    console.log(bomMappingDataTemp, MMmappingDataTemp);
    const body = {
      operationName: "mappingConfigDataMutation",
      variables: {
        tenant_id: parseInt(localStorage.getItem("tenantId")),
        bom: bomMappingDataTemp,
        material: MMmappingDataTemp,
      },
      query: mappingConfigDataMutation,
    };
    runMutation(DTX_GRAPHQL_URL, localStorage.getItem("tenantToken"), body)
      .then((data) => {
        if (data?.errors) {
          data.errors.map((element) => {
            if (element?.message.includes(JWT_ERROR_MESSAGE)) {
              logout();
              dispatch(setNextPage("homeLogin"));
            } else {
              showErrorMessage("mapping-updation-warning", "Something went wrong");
            }
          });
        } else {
          successMessage("mapping-updation-success", "Mapping configuration added Successfully!");
          getLatestMappingData();
        }
      })
      .catch((error) => {
        dispatch(setUploadLoader(false));
        console.log("Error", error);
        showErrorMessage("bom-name-check-warning", "Something went wrong");
      });
  };

  // deleteCurrentRow == > deletes the selected row from mapping data
  const deleteCurrentRow = (key) => {
    Excel.run((context) => {
      let selectedRange = context.workbook.worksheets
        .getActiveWorksheet()
        .getRange(String(mappingConfig[key].address + 1));
      selectedRange.format.fill.clear("color");
      nextPage === "bom" ? selectedRange.clear() : selectedRange.format.fill.clear("color");
      selectedRange.format.autofitRows();
      selectedRange.format.autofitColumns();
      return context.sync();
    });
    const MMconfigDataCopy = { ...mappingConfig };
    delete MMconfigDataCopy[key];

    if (Object.keys(MMconfigDataCopy).length) {
      dispatch(setmappingConfig2(MMconfigDataCopy));
    } else {
      dispatch(setmappingConfig2({}));
    }
  };
  return (
    <div className="material-map-box">
      <div id="app-div" className="app-div">
        <div className="select-map-feild">
          <DropDown
            options={fieldFor}
            handleChange={selectRangeFor}
            value={rangeFor}
            label={"Selelct the field"}
            noOptionMessage={"No mapping fields availble"}
          />
          <div className="action">
            <p className="warning" id="warning"></p>
          </div>
          <div className="action">
            {
              <span onClick={reserveSelectedSpace} id="apply-button">
                <Button label="Apply" />
              </span>
            }
          </div>
          <blockquote className="reserved-column-error" id="reserved-column-error"></blockquote>

          <table id="configure-table">
            <thead>
              <tr>
                <th>Mapping to </th>
                <th>Address </th>
                <th onClick={() => setEdit(!edit)}>
                  <img className="sidebar-logo-img" src={EditLogo} />
                </th>
              </tr>
            </thead>
            <tbody>
              {mappingConfig &&
                Object.entries(mappingConfig)?.map(
                  ([key, value]) =>
                    value?.address && (
                      <tr key={key}>
                        <td>
                          {value.name}
                          {key === "custom_material_id" ||
                          key === "material_name" ||
                          key === "plm_material_id" ||
                          key === "unit_of_material" ? (
                            <sup>*</sup>
                          ) : null}
                        </td>
                        <td>{value.address}</td>
                        {nextPage === "bom"
                          ? edit &&
                            value?.address !== "A" && (
                              <td
                                onClick={() => {
                                  deleteCurrentRow(key);
                                }}
                              >
                                <img className="sidebar-logo-img" src={CloseLogo} />
                              </td>
                            )
                          : edit && (
                              <td
                                onClick={() => {
                                  deleteCurrentRow(key);
                                }}
                              >
                                <img className="sidebar-logo-img" src={CloseLogo} />
                              </td>
                            )}
                      </tr>
                    )
                )}
            </tbody>
          </table>
          <div className="upload">
            <span
              onClick={() => {
                saveMMmappingConfig();
              }}
            >
              <Button label={"Save"} />
            </span>
          </div>
        </div>
      </div>
      <blockquote id="mapping-updation-warning" className="mapping-updation-warning"></blockquote>
      <blockquote id="mapping-updation-success" className="mapping-updation-success"></blockquote>
    </div>
  );
}

export default MaterialMap;
